import React, { useRef, useEffect, useState } from 'react';
import { connect } from 'react-redux';
import io from 'socket.io-client';
import Peer from 'simple-peer';
import serverConfig from '../../server-config';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import {
  faMicrophone,
  faMicrophoneSlash,
  faVideo,
  faPhoneSlash,
  faVideoSlash,
} from '@fortawesome/free-solid-svg-icons';

import './Call.css';

function Call(props) {
  const { user, p2p } = props;
  const { socket } = props;
  const localStream = useRef();
  const localVideo = useRef();
  const yourPeer = useRef();
  const anotherPeer = useRef();
  const [subscriber, setSubscriber] = useState(false);
  const [calling, setCalling] = useState();
  const [localAudioEnabled, setLocalAudioEnabled] = useState(true);
  const [localVideoEnabled, setLocalVideoEnabled] = useState(false);

  const fetchStreamFromMediaDevices = async () => {
    let stream;

    // if (navigator.mediaDevices) {
    stream = await navigator.mediaDevices.getUserMedia({ video: true, audio: true });
    // } else {
    // stream = navigator.getUserMedia({ video: true, audio: true });
    // }

    localVideo.current.srcObject = stream;
    localStream.current = stream;
    localStream.current.getVideoTracks()[0].enabled = localVideoEnabled;
  };

  useEffect(() => {
    if (!socket) return;

    socket.on('you have a call', (data) => {
      if (data.to.userId !== user._id) {
        return;
      }

      const stream = localStream.current;

      anotherPeer.current = new Peer({
        initiator: false,
        trickle: false,
        stream,
      });

      console.log('HERERERER');
    });
  }, [socket]);

  const tryCall = async (subscriber) => {
    fetchStreamFromMediaDevices();

    const peer = new Peer({
      initiator: true,
      trickle: false,
      stream: localStream.current,
    });

    yourPeer.current = peer;

    peer.on('signal', (signal) => {
      socket.emit('try call', {
        signalFromCaller: signal,
        from: { name: user.name, imageSrc: user.imageSrc, userId: user._id },
        to: {
          teamId: p2p.subscriber.teamId,
          userId: p2p.subscriber.whom._id,
        },
      });
    });
  };

  const handleVideoSwitch = () => {
    localStream.current.getVideoTracks()[0].enabled = !localStream.current.getVideoTracks()[0].enabled;
    setLocalVideoEnabled(!localVideoEnabled);
  };

  const handleAudioSwitch = () => {
    localStream.current.getAudioTracks()[0].enabled = !localStream.current.getAudioTracks()[0].enabled;
    setLocalAudioEnabled(!localAudioEnabled);
  };

  useEffect(() => {
    setCalling(props.p2p.tryCall);
    setSubscriber(props.p2p.subscriber);

    if (props.p2p.tryCall) {
      tryCall(props.p2p.subscriber);
    }
  }, [props.p2p.tryCall]);

  return (
    <div className={`call-container ${calling && 'show-mini'}`}>
      {props.p2p.tryCall && <span>Calling to {subscriber?.whom.name}</span>}
      <video id="localVideo" ref={localVideo} autoPlay muted />
      <div className="call-buttons">
        <div
          style={{ background: localAudioEnabled ? '#42AF2C' : '#DF3A3A' }}
          onClick={handleAudioSwitch}
          className="micro-switch-btn call-btn"
        >
          <FontAwesomeIcon icon={localAudioEnabled ? faMicrophone : faMicrophoneSlash} />
        </div>
        <div
          style={{ background: localVideoEnabled ? '#42AF2C' : '#DF3A3A' }}
          onClick={handleVideoSwitch}
          className="video-switch-btn call-btn"
        >
          <FontAwesomeIcon icon={localVideoEnabled ? faVideo : faVideoSlash} />
        </div>
        <div style={{ background: '#DF3A3A' }} className="end-call-btn call-btn">
          <FontAwesomeIcon icon={faPhoneSlash} />
        </div>
      </div>
    </div>
  );
}

export default connect((state) => ({ p2p: state.p2p, socket: state.socket.socket, user: state.user.userData }))(Call);
