import React, { useContext, useRef, useState, useEffect } from 'react';
import { Formik, Form, ErrorMessage, Field } from 'formik';
import ThemedField from '../../shared/ThemedField/ThemedField';
import Button from '../../shared/Button/Button';
import { connect } from 'react-redux';
import { uploadPhoto } from '../../actions/user';
import FileInput from '../../shared/FileInput/FileInput';
import serverConfig from '../../server-config';

import './Settings.css';

function Settings(props) {
  const { user } = props;
  const [userImageSrc, setUserImageSrc] = useState('');
  const emailRegex = /^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,}$/i;

  useEffect(() => {
    setUserImageSrc(serverConfig.baseUrl + '/' + user.imageSrc);
  }, [props]);

  const handleFileSave = (formData) => {
    props.dispatch(uploadPhoto(user._id, formData, props.jwt));
  };

  return (
    user && (
      <div className="app-profile-settings">
        <Formik
          initialValues={{ email: user.email, password: '' }}
          validate={(values) => {
            const errors = {};

            if (!values.email) {
              errors.email = 'Email is required';
            } else if (!emailRegex.test(values.email)) {
              errors.email = 'Invalid email address';
            }

            if (!values.password) {
              errors.password = 'Password is required';
            }

            return errors;
          }}
          // onSubmit={(values) => props.onSubmit(values)}
        >
          {() => (
            <Form className="app-profile-settings-form ">
              <FileInput defaultSrc={userImageSrc} options={['save', 'discard']} onFileSave={handleFileSave} />

              <ThemedField>
                <label>Your email</label>
                <Field placeholder="abc@gmail.com" type="text" name="email" />
                <ErrorMessage name="email" component="span" />
              </ThemedField>
              <ThemedField>
                <label>Your password</label>
                <Field type="password" name="password" />
                <ErrorMessage name="password" component="span" />
              </ThemedField>
              {props.children}
              <Button type="submit">Save</Button>
            </Form>
          )}
        </Formik>
      </div>
    )
  );
}

export default connect((state) => ({ user: state.user.userData, jwt: state.auth.token }))(Settings);
