import React, { useContext, useEffect, useState } from 'react';
import './ProfileView.css';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faCamera, faEnvelope } from '@fortawesome/free-solid-svg-icons';
import { ThemeContext } from '../../core/Page/Page';
import Button from '../../shared/Button/Button';
import { Link } from 'react-router-dom';
import colors from '../../colors';
import Emoji from '../../shared/Emoji';
import serverConfig from '../../server-config';

function ProfileView(props) {
  const { theme } = useContext(ThemeContext);
  const { user } = props;

  const handleInviteClick = () => {};

  const handleInvitationAction = async (action, teamId) => {
    await fetch(`${serverConfig.baseUrl}/users/invitations/${action}/${teamId}`, {
      headers: {
        Authorization: `Bearer: ${props.token}`,
      },
    });
  };

  const handleAcceptClick = (teamId) => {
    handleInvitationAction('accept', teamId);
  };

  const handleDenyClick = (teamId) => {
    handleInvitationAction('deny', teamId);
  };

  const mapTeams = () => {
    return user.teams.map((team) => {
      return (
        <li style={{ borderColor: colors[theme].foreground }} key={team._id} className="team-mini">
          <div style={{ backgroundImage: `url(${serverConfig.baseUrl}/${team.imageSrc})` }} className="img-wrap"></div>
          <Link style={{ color: colors[theme].foreground }} to={`/teams/thread/${team._id}`}>
            {team.name}
          </Link>
        </li>
      );
    });
  };

  const renderTeams = () => {
    if (user.teams && user.teams.length > 0) {
      return (
        <div>
          <ul className="teams-list">{mapTeams(user.teams)}</ul>
        </div>
      );
    } else {
      return (
        <h3 style={{ borderColor: colors.common.primary, marginLeft: '15px' }} className="h3-no-teams">
          <Emoji label="robot">ðŸ¤–</Emoji> You are not on any team yet
        </h3>
      );
    }
  };

  const mapInvitations = () => {
    return user.invitations.map((inv) => {
      return (
        <li key={inv.teamId}>
          <Link to={`/team/${inv.teamId}`} style={{ color: colors[theme].foreground, textDecoration: 'underline' }}>
            {inv.name}
          </Link>
          <button onClick={() => handleAcceptClick(inv.teamId)} className="accept-invitation-btn">
            Accept
          </button>
          <button onClick={() => handleDenyClick(inv.teamId)} className="deny-invitation-btn">
            Deny
          </button>
        </li>
      );
    });
  };

  return (
    <div className="app-profile-view">
      <div className="flex-row">
        <div className="left-section">
          <div
            style={{ backgroundImage: `url(${serverConfig.baseUrl}/${user.imageSrc})` }}
            className={`profile-img profile-img-${theme}`}
          >
            {!user.imageName && <FontAwesomeIcon icon={faCamera} />}
          </div>

          <div>
            {props.owner ? (
              <>
                <div style={{ display: 'flex', flexDirection: 'column', alignItems: 'center' }}>
                  {user.invitations.length > 0 && <h2 className="invitations-h2">Your invitations:</h2>}
                  <ul>{mapInvitations()}</ul>
                  {user.emailConfirmed ? (
                    <Button style={{ marginTop: '25px' }}>
                      <Link to="/teams/create">Create a team</Link>
                    </Button>
                  ) : (
                    <Button style={{ marginTop: '25px' }}>
                      <Link to="/re-confirm">Confirm email</Link>
                    </Button>
                  )}
                </div>
              </>
            ) : (
              <div className="guest-section">
                {user.emailConfirmed && (
                  <Button onClick={handleInviteClick} style={{ marginTop: '20px' }}>
                    Invite to the team
                  </Button>
                )}
              </div>
            )}
          </div>
        </div>

        <div style={{ display: 'flex', flexDirection: 'column', alignItems: 'flex-start' }}>
          <h1 className="profile-name">{user.name}</h1>
          <h2 className="profile-email">
            <FontAwesomeIcon style={{ marginRight: '20px' }} icon={faEnvelope} />
            {user.email}
            {!user.emailConfirmed && <span style={{ color: 'red', margin: '0 10px' }}>(not confirmed)</span>}

            <span style={{ marginLeft: '10px', color: theme === 'dark' ? '#171d2d' : '#b2b2b2' }}>#{user.uuid}</span>
          </h2>

          {props.owner && <div className="teams-container">{renderTeams()}</div>}
        </div>
      </div>
    </div>
  );
}

export default ProfileView;
