import React, { useEffect, useState } from 'react';
import TeamSettings from './TeamSettings';
import serverConfig from '../../../server-config';
import { connect } from 'react-redux';

function TeamSettingsContainer(props) {
  const { teamId } = props.match.params;
  const [teamInfo, setTeamInfo] = useState(false);

  const fetchTeamInfo = async () => {
    const response = await fetch(`${serverConfig.baseUrl}/teams/get/by/id/${teamId}`);
    const json = await response.json();

    setTeamInfo(json);
  };

  useEffect(() => {
    fetchTeamInfo();
  }, []);

  return teamInfo && <TeamSettings onTeamChanged={fetchTeamInfo} token={props.token} team={teamInfo} />;
}

export default connect((state) => ({ token: state.auth.token }))(TeamSettingsContainer);
